/* 2. Header */
.basic-bar {
  height: 60px;
  width: 60px;
  border-radius: 50%;
  margin-right: 50px;
  padding: 14px 0;
  cursor: pointer;
  background: rgba($primaryColor, 0.3);
}

.basic-bar span {
  height: 2px;
  width: 15px;
  display: block;
  background: $primaryColor;
  margin: auto;
  margin-top: 6px;
  margin-bottom: 6px;
  transition: 0.3s;
}

.basic-bar:hover span {
  width: 30px;
}

.basic-bar span.bar1 {}

.basic-bar span.bar2 {
  width: 30px;
}

.basic-bar span.bar3 {}

.transparent-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9;
}

.header-info span {
  position: relative;
  margin-right: 29px;
  font-size: 14px;
  color: #666;
}

.header-info span::before {
  background: #ddd;
  content: "";
  height: 21px;
  position: absolute;
  right: -18px;
  top: 0px;
  width: 1px;
}

.header-info span:last-child:before {
  display: none;
}

.header-right-member {}

.header-right-member ul {}

.header-right-member ul li {
  display: inline-block;
  margin-left: 25px;
  position: relative;
  padding: 12px 0px;
}

.header-right-member ul li a {
  font-size: 14px;
  color: #666;
}

.header-right-member ul li:hover>a {
  color: $primaryColor;
}

.header-right-member ul li ul {
  position: absolute;
  top: 100%;
  left: 0;
  background: #444;
  width: 110px;
  text-align: left;
  padding: 10px;
  transition: 0.3s;
  visibility: hidden;
  opacity: 0;
  z-index: 9;
}

.header-right-member ul li:hover ul {
  visibility: visible;
  opacity: 1;
}

.header-right-member ul li ul li {
  margin: 0;
  display: block;
  padding: 3px 0;
}

.header-right-member ul li ul li a {
  color: $white;
  font-size: 12px;
}

/* main-menu */

.main-menu ul li {
  display: inline-block;
  margin: 0 25px;
  line-height: 1;
  position: relative;
}

.main-menu ul li a {
  color: #10111e;
  font-size: 14px;
  line-height: 1;
  font-weight: 500;
  padding: 60px 0;
  display: inline-block;
  text-transform: uppercase;
}

.main-menu ul li:hover>a {
  color: $primaryColor;
}

.main-menu ul li ul.submenu {
  position: absolute;
  top: 100%;
  left: 0;
  // background: $white;
  width: 250px;
  transition: 0.3s;
  opacity: 0;
  visibility: hidden;
  z-index: 9;
  text-align: left;
  box-shadow: 0px 12px 24px 0px rgba($primaryColor, 0.3);
}

.main-menu ul li ul.submenu li {
  margin: 0;
  display: block;
  border-bottom: 1px solid #b0c6c64d;
}

.main-menu ul li ul.submenu li:last-child {
  border: 0;
}

.main-menu ul li ul.submenu li a {
  padding: 16px 20px;
  // color: #10111e;
  color: $white;
  font-size: 12px;
  display: block;
}

.main-menu ul li ul.submenu li:hover>a {
  color: $primaryColor;
}

.main-menu ul li:hover>ul.submenu {
  opacity: 1;
  visibility: visible;
}

.main-menu ul li ul.submenu li ul.submenu {
  left: 100%;
  top: 0;
  opacity: 0;
  visibility: hidden;
}

.main-menu ul li ul.submenu li:hover>ul.submenu {
  opacity: 1;
  visibility: visible;
}

.main-menu.menu-white ul li:last-child {
  margin: 0;
}

/* mega-menu */
.main-menu ul li.mega-menu>ul.submenu {
  min-width: 800px;
  opacity: 0;
  visibility: hidden;
  padding: 35px;
  z-index: 0;
  background-image: url('../imgs/bg/flower-bg.png');
  background-size: cover;
  background-repeat: no-repeat;
  color: $white;
}

.main-menu ul li.mega-menu:hover ul.submenu {
  visibility: visible;
  opacity: 1;
  z-index: 11;

}

.main-menu ul li.mega-menu ul.submenu li ul.submenu {
  position: static;
  display: block;
  border: none;
  margin-top: 20px;
  opacity: 0;
  visibility: hidden;
  width: auto;
  box-shadow: none;
}

.main-menu ul li.mega-menu:hover ul.submenu li ul.submenu {
  visibility: visible;
  opacity: 1;
  z-index: 11;
}

.main-menu ul li.mega-menu ul.submenu li ul.submenu li a {
  font-size: 13px;
  text-transform: capitalize;
  font-weight: 400;
  padding: 13px 0;
}

.main-menu ul li.mega-menu ul.submenu li ul.submenu li:last-child a {
  padding-bottom: 0;
}

.main-menu ul li.mega-menu ul.submenu li ul.submenu li:last-child {
  border-bottom: 0;
}

.main-menu ul li.mega-menu ul.submenu>li {
  border: 0;
}

.main-menu ul li.mega-menu>ul.submenu>li {
  width: 33.3333%;
  min-width: 33.3333%;
  display: block;
  float: left;
  padding-right: 30px;
}

.main-menu ul li.mega-menu>ul.submenu>li:last-child {
  padding-right: 0px;
}

.main-menu ul li.mega-menu>ul.submenu ul.submenu {
  margin-top: 20px;
}

.main-menu ul li.mega-menu>ul.submenu li a {
  display: block;
  padding-left: 0;
}

.main-menu ul li.mega-menu>ul.submenu>li>a {
  font-size: 12px;
  line-height: inherit;
  font-weight: 600;
  // color: #10111e;
  color: $white;
  padding: 0;
  text-transform: uppercase;
}

#header-sticky {
  transition: 0.3s;
}

.logo {
  transition: 0.3s;

  img {
    max-width: 10rem;
  }
}

.header-right {
  transition: 0.3s;
}

.sticky-header {
  animation: 300ms ease-in-out 0s normal none 1 running fadeInDown;
  background: $white;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  left: 0;
  margin: 0 auto;
  position: fixed;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 99;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

.sticky-header .main-menu ul li a {
  padding: 35px 0;
}

/* header-right */
.header-right {
  position: relative;
  padding-left: 10px;
  margin-left: 10px;
}

.header-right ul li {
  display: inline-block;
  margin-left: 10px;
  line-height: 1;
  position: relative;
}

.header-right>ul>li>a {
  font-size: 16px;
  position: relative;
  height: 50px;
  width: 50px;
  background: rgba($primaryColor, 0.2);
  display: inline-block;
  line-height: 50px;
  text-align: center;
  border-radius: 50%;
  color: $primaryColor;
}

.header-right ul li a:hover {
  background: $primaryColor;
  color: $white;
}

.cart-count {
  position: absolute;
  background: $primaryColor;
  color: $white;
  height: 20px;
  width: 20px;
  font-size: 10px;
  text-align: center;
  font-weight: 500;
  border-radius: 50%;
  line-height: 22px;
  left: 32px;
  top: -8px;
}

/* minicart */
.header-right ul.minicart {
  background: $white;
  background-image: url('../imgs/bg/flower-bg.png');
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0;
  padding: 25px;
  position: absolute;
  right: 0;
  top: 100%;
  transition: all 0.3s ease 0s;
  width: 350px;
  z-index: 9;
  box-shadow: 0px 12px 24px 0px rgba($primaryColor, 0.3);
  visibility: hidden;
}

.header-right li:hover ul.minicart {
  opacity: 1;
  visibility: visible;
  z-index: 99;
}

.header-right ul li .minicart .cart-img {
  float: left;
}

.header-right ul li .minicart .cart-content {
  float: left;
  padding-left: 15px;
  text-align: left;
}

.cart-content h3 {
  color: #ddd;
}

ul.minicart .cart-content a {
  // color: #10111e;
  color: $white;
  font-size: 14px;
  background: none;
}

ul.minicart .cart-content a:hover {
  color: $primaryColor;
  background: none;
}

ul.minicart .cart-img img {
  width: 85px;
}

ul.minicart .cart-price span {
  color: #ddd;
  font-size: 13px;
}

ul.minicart .cart-price .new {
  font-size: 14px;
  // color: #747691;
  color: $white;
}

ul.minicart .cart-price span {
  // color: #a5a7bc;
  color: $white;
  font-size: 13px;
  font-weight: 500;
}

.header-right ul li .minicart>li {
  display: block;
  margin-bottom: 22px;
  margin-left: 0;
  overflow: hidden;
  padding: 0;
}

.header-right ul li .minicart>li:last-child {
  margin-bottom: 0;
}

.header-right ul li .minicart .del-icon>a {
  // color: $primaryColor;
  color: $white;
}

.header-right ul li .minicart .del-icon>a:hover {
  color: #a5a7bc;
  background: none;
}

.header-right ul li .minicart .del-icon {
  float: right;
  margin-top: 30px;
}

.total-price span {
  // color: #747691;
  color: $white;
}

.total-price {
  // border-top: 1px solid #cacadb;
  overflow: hidden;
  padding-top: 25px;
  margin-top: 10px;
}

.header-right ul li .minicart .checkout-link a {
  background: $secondaryColor;
  color: $white;
  display: block;
  font-weight: 500;
  padding: 16px 30px;
  text-align: center;
  font-size: 13px;
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.header-right ul li .minicart .checkout-link a:last-child {
  margin: 0;
}

.header-right ul li .minicart .checkout-link a:hover,
.header-right ul li .minicart .checkout-link a.red-color {
  background: $primaryColor;
}

.header-right ul li .minicart .checkout-link a.red-color:hover {
  background: $secondaryColor;
}

.header-sep {
  line-height: 1;
  height: 2px;
}

/* menu white */
.menu-white ul li a {
  color: $white;
}

.menu-white {}

/* header 3 */
/* extra info */
.extra-info {
  background: #152136;
  background-image: url('../imgs/bg/flower-bg.png');
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  transition: all 0.7s ease 0s;
  width: 340px;
  z-index: 9999;
  transform: translateX(-100%);
  padding-bottom: 40px;
}

.extra-info.info-open {
  transform: translateX(0);
}

.extra-info.extra-info-left.info-open {
  transform: translateX(0);
}

.close-icon {
  margin-top: 0;
  text-align: right;
  position: absolute;
  right: 15px;
  top: 12px;
  line-height: 1;
}

.close-icon>button {
  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
  border: 0 none;
  color: $primaryColor;
  cursor: pointer;
  font-size: 20px;
  padding: 0;
}

.logo-side {
  padding: 30px 30px;
  // border-bottom: 1px solid #344159;
  margin-bottom: 24px;
}

.side-info-bottom {
  padding: 0 30px;
  // border-top: 1px solid #344159;
  padding-top: 30px;
}

.side-cta p {
  color: #aaabc3;
}

.side-cta h4 {
  color: $white;
}

.social-icon-right a {
  color: $white;
  margin-right: 15px;
}

.social-icon-right a:hover {
  color: $primaryColor;
}

.side-menu ul li {
  display: block;
  margin: 0px;
  padding: 0 30px;
}

.side-menu ul li a {
  color: $white;
  padding: 20px 0;
  display: block;
}

.side-menu ul li a i {
  float: right;
}

.side-menu ul li ul.submenu {
  top: 0;
  left: 100%;
}

.side-menu ul li ul.submenu li {
  text-align: left;
  padding: 0 0;
}

/* category-menu */
.category-menu {
  position: absolute;
  top: 150px;
  z-index: 999;
  background: $white;
  width: 250px;
  padding: 0 30px;
  left: 15px;
  display: none;
  box-shadow: 0px 12px 24px 0px rgba(254, 69, 54, 0.3);
}

.category-menu h4 {
  margin-bottom: 20px;
  padding-top: 25px;
}

.category-menu ul {
  margin-bottom: 30px;
  border-left: 3px solid rgba($primaryColor, 0.2);
  padding-left: 20px;
  margin-bottom: 30px;
}

.category-menu ul li {}

.category-menu ul li:last-child {
  margin-bottom: 0px;
}

.category-menu ul li a {
  display: block;
  color: #747691;
  position: relative;
  padding: 10px 0;
}

.category-menu ul li a::before {
  position: absolute;
  height: 0;
  width: 4px;
  background: $primaryColor;
  left: -24px;
  top: auto;
  bottom: 0;
  content: "";
  transition: 0.3s;
}

.category-menu ul li a:hover:before {
  height: 100%;
  top: 0;
  bottom: auto;
}

.category-menu ul li a:hover {
  color: $primaryColor;
}

.category-menu ul li a i {
  color: $primaryColor;
  width: 21px;
  display: inline-block;
}

.header-area {
  position: relative;
  margin-bottom: 3rem;
}

.flower-bar {
  height: 2rem;
  background-image: url('../imgs/bg/flower-bg.png');
  width: 100%;
}