/* button style */

.btn {
  background: transparent;
  border: 0;
  border-radius: 0;
  color: #525470;
  display: inline-block;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: 2px;
  line-height: 1;
  margin-bottom: 0;
  padding: 22px 50px;
  text-align: center;
  text-transform: uppercase;
  touch-action: manipulation;
  transition: all 0.3s ease 0s;
  vertical-align: middle;
  white-space: nowrap;
}
.theme-btn {
  background: $primaryColor;
  color: $white;
}
.theme-btn:hover {
  background: $secondaryColor;
  color: $white;
}
.theme-btn-2 {
  background: $secondaryColor;
  color: $white;
}
.theme-btn-2:hover {
  background: $primaryColor;
  color: $white;
}
.white-btn {
  background: $white;
}
.white-btn:hover {
  background: $secondaryColor;
  color: $white;
}
.breadcrumb > .active {
  color: #888;
}
.owl-carousel .owl-nav div {
  background: rgba(255, 255, 255, 0.8) none repeat scroll 0 0;
  height: 40px;
  left: 20px;
  line-height: 40px;
  opacity: 0;
  position: absolute;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.3s ease 0s;
  visibility: hidden;
  width: 40px;
}
.owl-carousel .owl-nav div.owl-next {
  left: auto;
  right: 20px;
}
.owl-carousel:hover .owl-nav div {
  opacity: 1;
  visibility: visible;
}
.owl-carousel .owl-nav div:hover {
  background: #2b96cc;
  color: $white;
}
