/* 4. Banner */
.banner {
  overflow: hidden;
  position: relative;

  h4 {
    position: absolute;
    bottom: 5%;
    left: 5%;
    text-transform: capitalize;
    color: rgba($primaryColor, 0.8);
    font-size: 4.5rem;
  }
}

.banner:hover img {
  transform: scale(1.05);
}

.banner-trend {
  position: absolute;
  bottom: 45px;
  left: 45px;
  right: 45px;
  background: $white;
  padding: 45px;
}

.banner-trend span {
  font-size: 14px;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-weight: 500;
  color: $secondaryColor;
  display: block;
  margin-bottom: 10px;
}

.banner-trend h2 {
  font-size: 30px;
}

.banner-trend h2 a:hover {
  color: $primaryColor;
}

.discover-link a {
  color: $primaryColor;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 2px;
}

.discover-link a:hover {
  color: $secondaryColor;
}

.discover-link {
  position: absolute;
  right: 45px;
  top: 50%;
  transform: translateY(-50%);
}

.banner-look {
  position: absolute;
  top: 40px;
  right: 40px;
  background: $white;
  padding: 40px;
  border-top: 3px solid $secondaryColor;
  width: 410px;
}

.banner-look span {
  font-size: 14px;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-weight: 500;
  color: $secondaryColor;
  display: block;
  margin-bottom: 10px;
}

.banner-look h2 {
  font-size: 40px;
  margin-bottom: 15px;
}

.banner-look img {
  display: block;
  margin-bottom: 15px;
}

.banner-look p {
  margin-bottom: 20px;
}