/* 10. Footer */
footer {
  border-top: 1px solid #eee;

  .copyright-area {
    border-top: 1px solid #eee;
  }

}

.footer-logo {
  margin-bottom: 10px;

  img {
    max-width: 300px;
  }
}

.footer-widget h3 {
  font-size: 20px;
  margin-bottom: 40px;
  line-height: 1;
  text-transform: capitalize;
}

.footer-widget p {
  color: #747691;
}

.time-icon {
  margin-right: 15px;
  margin-top: 5px;
}

.time-text span {
  color: #747691;
  margin-bottom: 5px;
  display: block;
}

.time-text h2 {
  font-weight: 300;
  font-size: 30px;
  margin: 0;
}

.footer-icon a {
  color: #b9bad4;
  margin-left: 20px;
  font-size: 16px;
}

.footer-icon a:hover {
  color: $primaryColor;
}

.footer-link li {
  margin-bottom: 15px;
}

.footer-link li:last-child {
  margin-bottom: 0;
}

.footer-link li a {
  color: #747691;
  font-size: 14px;
}

.footer-link li a:hover {
  color: $primaryColor;
}

.quick-links {
  text-align: center;

  li {
    display: inline-block;

    &:after {
      content: "•";
      color: $primaryColor;
      font-size: 2.5rem;
      vertical-align: middle;
      margin: 0 0.5rem;


    }

    &:last-child {
      &:after {
        display: none;
      }
    }

    a {
      font-size: 16px;
    }
  }
}


.footer-social {

  li {
    display: inline-block;
  }

  i {
    margin-right: 5px;
    font-size: 2rem;
  }
}

.contact-list li i {
  color: #717171;
  width: 29px;
  float: left;
  position: relative;
  top: 7px;
}

.contact-list li p {
  color: #878787;
  overflow: hidden;
  font-size: 14px;
  display: block;
  margin: 0;
}

.contact-list li {
  margin-bottom: 10px;
}

.copyright-border {
  border-top: 1px solid #d1d2dd;
}

.copyright p {
  margin-bottom: 0;
  color: #747691;
}

.copyright p a {
  color: $primaryColor;
}

.copyright p a:hover {
  color: $secondaryColor;
}

.subscribe-footer {
  position: relative;
}

.subscribe-footer input {
  height: 50px;
  background: no-repeat;
  border: 1px solid #444;
  padding: 15px;
  width: 100%;
  color: $white;
}

.subscribe-footer button {
  background: none;
  border: 0;
  top: 0;
  right: 0;
  position: absolute;
  color: #888;
  height: 100%;
  line-height: 1;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 500;
  padding: 0 15px;
}

.instagram-list {
  margin: 0 -2px;
}

.instagram-list li {
  float: left;
  padding: 0 2px;
  width: 33.33%;
  margin-bottom: 6px;
}