.instagram-area {
    .instagram-btn {
        position: absolute;
        left: 0;
        right: 0;
        text-align: center;
        top: 50%;
        transform: translateY(-50%);
        z-index: 1;
    }

    .instagram-btn a i {
        color: #fe4536;
        transition: .3s;

        &:hover {
            color: #fff;
        }
    }

}