/* 13. Shop */
.product-showing p {
	margin: 0;
	border: 2px solid #f2f2f2;
	padding: 17px 30px;
	text-align: center;
	line-height: 1;
	display: inline-block;
	text-transform: uppercase;
	font-size: 12px;
	letter-spacing: 2px;
	font-weight: 500;
	color: #747691;
}

@media (max-width: 767px) {
	.product-showing p {
		padding: 17px 15px;
	}
}

.pro-filter {
	position: relative;
	display: inline-block;
}

@media (max-width: 767px) {
	.pro-filter {
		float: left;
	}
}

.pro-filter select {
	border: 2px solid #f2f2f2;
	padding: 17px 30px;
	display: inline-block;
	text-transform: uppercase;
	font-size: 12px;
	letter-spacing: 2px;
	line-height: 1;
	color: #6f7172;
	appearance: none;
	-moz-appearance: none;
	width: 145px;
	font-weight: 500;
}

.pro-filter::before {
	content: "\f107";
	right: 15px;
	top: 15px;
	position: absolute;
	color: #758799;
	font-family: "Font Awesome 5 Free";
	font-weight: 700;
}

.shop-tab ul li {
	margin-left: 20px;
}

@media (max-width: 767px) {
	.shop-tab ul li {
		margin-left: 10px;
	}
}

.shop-tab ul li a {
	font-size: 16px;
	font-weight: 500;
	color: $primaryColor;
	letter-spacing: 2px;
	padding: 0;
	text-transform: uppercase;
	position: relative;
	height: 50px;
	width: 50px;
	border-radius: 50%;
	background: rgba($primaryColor, 0.2);
	line-height: 50px;
	text-align: center;
}

.shop-tab ul li a.active {
	color: white;
	background: $primaryColor;
	box-shadow: 0px 8px 16px 0px rgba($primaryColor, 0.3);
}

.pro-list-content .product-action {
	position: static;
	opacity: 1;
	visibility: visible;
}

.pro-list-content .product-action a {
	background: $secondaryColor;
	color: $white;
	border-color: $secondaryColor;
}

.pro-list-content .product-action a:hover {
	background: $primaryColor;
	color: $white;
	border-color: $primaryColor;
}

.pro-list-content p {
	margin-bottom: 22px;
}

.pro-list-content {
}

.pro-list-content {
}

/* shop widget */
.sidebar-box {
	padding: 35px 40px;
	border: 4px solid #f9f9f9;
}

.shop-widget {
	padding-bottom: 40px;
	margin-bottom: 40px;
	border-bottom: 2px dashed #ede8ff;
}

.shop-widget:last-child {
	padding-bottom: 0px;
	margin-bottom: 0px;
	border-bottom: 0;
}

.shop-title {
	font-size: 20px;
	text-transform: uppercase;
	margin-bottom: 30px;
}

.shop-search {
	position: relative;
}

.shop-search input {
	width: 100%;
	height: 60px;
	border: 0;
	background: #f9f9f9;
	border-radius: 30px;
	padding: 0 30px;
}

.shop-search button {
	position: absolute;
	background: $primaryColor;
	border: 0;
	color: $white;
	height: 60px;
	width: 60px;
	right: 0;
	top: 0;
	border-radius: 50%;
	box-shadow: 0px 8px 16px 0px rgba($primaryColor, 0.3);
}

.shop-search button:hover {
	background: $secondaryColor;
}

.shop-link li {
	margin-bottom: 15px;
}

.shop-link li:last-child {
	margin-bottom: 0px;
}

.shop-link li a {
	color: #747691;
}

.shop-link li a i {
	color: #e7e7e7;
	margin-right: 10px;
	line-height: 1;
	transition: 0.3s;
}

.shop-link li a:hover i,
.shop-link li a:hover {
	color: $primaryColor;
}

.price-filter label {
	display: inline-block;
	margin-right: 16px;
	position: relative;
	top: -23px;
}

.price-lebel {
	display: inline-block;
}

#slider-range {
	background: #ece8fe;
	border: 0 none;
	border-radius: 0;
	height: 5px;
}

#slider-range span.ui-state-default {
	background: $secondaryColor;
	border: 0 none;
	border-radius: 50%;
	height: 15px;
	width: 15px;
}

.ui-slider-horizontal .ui-slider-range {
	background: $secondaryColor;
}

#slider-range span.ui-state-default {
}

.price-filter input {
	border: 0 none;
	color: #444444;
	margin-top: 20px;
}

.shop-tag li {
	display: inline-block;
	margin-right: 5px;
	margin-bottom: 6px;
}

.shop-tag li a {
	border: 1px solid #e7e7e7;
	padding: 5px 15px;
	display: inline-block;
	color: #747691;
	font-size: 14px;
}

.shop-tag li a:hover {
	background: $primaryColor;
	color: $white;
	border-color: $primaryColor;
}

.shop-link li span {
	height: 16px;
	width: 16px;
	background: transparent;
	display: inline-block;
	border-radius: 50%;
	border: 4px solid $white;
	box-shadow: 0px 1px 10px 0px rgba($primaryColor, 0.2);
	position: relative;
	top: 2px;
	margin-right: 10px;
}

.shop-link li span.blue {
	background: #0066ff;
}

.shop-link li span.green {
	background: #61a344;
}

.shop-link li span.orange {
	background: #ff9600;
}

.shop-link li span.navy {
	background: navy;
}

.shop-link li span.pinkish {
	background: #ff0090;
}

.shop-link li span.vista {
	background: #00fff6;
}

.side-pro-img {
	float: left;
}

.side-pro-img img {
	margin-right: 14px;
}

.side-pro-rating {
	margin-bottom: 5px;
}

.side-pro-rating i {
	color: #ffba00;
	font-size: 12px;
}

.side-pro-content h5 {
	font-size: 14px;
	margin-bottom: 7px;
}

.side-pro-content h5 a:hover {
	color: $primaryColor;
}

.side-pro-price span {
	font-weight: 500;
	font-size: 13px;
	color: #747691;
}

.side-pro-content {
	overflow: hidden;
}

.shop-sidebar-product li {
	overflow: hidden;
	margin-bottom: 20px;
}

.shop-sidebar-product li:last-child {
	margin-bottom: 0px;
}

/* shop widget filter */
.filter-widget {
	border: 4px solid #f9f9f9;
	padding: 30px;
	display: none;
	padding-bottom: 0;
}

.filter-widget .shop-widget {
	margin-bottom: 30px;
}

.pro-filter-btn button {
	background: #f9f9f9;
	border: 0;
	padding: 18px 50px;
	line-height: 1;
	border-radius: 30px;
	text-transform: uppercase;
	font-weight: 500;
	letter-spacing: 2px;
	color: #747691;
}

.mobile-filter {
	display: none;
}

@media (max-width: 767px) {
	.mobile-filter-wrap {
		position: fixed;
		bottom: 0;
		width: 100%;
		z-index: 999;

		.mobile-filter {
			width: 100%;
			height: 60px;
			display: flex;
			align-items: center;
			justify-content: center;

			i {
				margin-right: 5px;
			}
		}

		.hide {
			display: none;
			transition: all 0.2s ease-in-out;
			-webkit-transition: all 0.2s ease-in-out;
			-moz-transition: all 0.2s ease-in-out;
			-ms-transition: all 0.2s ease-in-out;
			-o-transition: all 0.2s ease-in-out;
		}

		.sidebar-box {
			background-color: $white;
			max-height: 550px;
			overflow: auto;
		}
	}
}
