/* 8. Upcoming Product */
.upc-price {
  color: $primaryColor;
  font-weight: 500;
  letter-spacing: 2px;
  font-size: 16px;
  margin-bottom: 20px;
}
.upcoming-product h1 {
  font-size: 80px;
  margin-bottom: 35px;
}
.upcoming-product h1 a:hover {
  color: $secondaryColor;
}
.upc-pro-info li {
  float: left;
  margin-right: 40px;
  margin-bottom: 30px;
}
.upc-pro-info li i {
  font-size: 40px;
  line-height: 1;
  margin-right: 20px;
  color: $secondaryColor;
}
.upc-info h4 {
  font-size: 20px;
  margin-bottom: 12px;
}
.upc-info span {
  color: #747691;
}
.upcoming-product p {
  margin-bottom: 30px;
}
.upc-btn a {
  margin-right: 20px;
}
.cdown {
  display: inline-block;
  height: 110px;
  width: 110px;
  border-radius: 50%;
  line-height: 110px;
  text-align: center;
  font-weight: 500;
  padding: 28px 0;
  background: $white;
  margin-right: 15px;
  box-shadow: 0px 16px 32px 0px rgba(224, 226, 255, 0.3);
}
@media (max-width: 767px) {
  .cdown {
    margin-right: 10px;
    margin-bottom: 10px;
  }
}
@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .cdown {
    height: 90px;
    width: 90px;
    padding: 18px 0;
  }
}

.cdown span {
  font-size: 30px;
  color: #10111e;
  line-height: 1;
  display: block;
  margin-bottom: 10px;
}

.cdown p {
  font-size: 12px;
  color: #747691;
  line-height: 1;
  margin: 0;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 1px;
}

.bakix-map {
  position: relative;
}

.bakix-marker {
  position: absolute;
}

.map-c1 {
  top: 40%;
  left: 7%;
}
.map-c2 {
  bottom: 26%;
  left: 34%;
}
.map-c3 {
  top: 32%;
  left: 25%;
}

/* style 2 */
.map-c11 {
  top: 40%;
  left: 22%;
}
.map-c33 {
  top: 51%;
  left: 38%;
}
.map-c22 {
  bottom: 7%;
  left: 29%;
}

.bakix-map-icon {
  background: $secondaryColor;
  height: 35px;
  width: 35px;
  border-radius: 50%;
  cursor: pointer;
  text-align: center;
  line-height: 34px;
  color: $white;
}
.bakix-map-address {
  background: $white;
  padding: 12px;
  margin-top: 10px;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  position: absolute;
  z-index: 9;
  width: 200px;
  top: 100%;
}
.bakix-map-address p {
  color: #747691;
  margin: 0;
}

.bakix-marker:hover .bakix-map-address {
  opacity: 1;
  visibility: visible;
}
