/* 14. Shop Details */
.shop-tab ul li {
  margin-left: 20px;
}
.shop-tab ul li a {
  font-size: 16px;
  font-weight: 500;
  color: #6f7172;
  letter-spacing: 2px;
  padding: 0;
  text-transform: uppercase;
  position: relative;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background: #f6f6ff;
  line-height: 50px;
  text-align: center;
}
.product-large-img img {
  width: 100%;
}
.shop-thumb-tab ul {
  margin: 0 -5px;
}
.shop-thumb-tab ul li {
  margin-bottom: 20px;
  display: inline-block;
  width: 33.33%;
  padding: 0 5px;
}
.shop-thumb-tab ul li a {
  padding: 0;
}
.product-details-img {
  margin-left: 0;
  overflow: hidden;
}

/* details info */
.details-cat a {
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 2px;
  color: #747691;
}
.details-cat a:hover {
  color: $primaryColor;
}
.details-price span {
  color: $primaryColor;
  font-weight: 300;
  font-size: 40px;
  margin-right: 25px;
}
.details-price .old-price {
  color: #d9dbec;
  text-decoration: line-through;
}
.variant-name span {
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 2px;
}

.variant-name {
  display: inline-block;
  margin-right: 30px;
}
.shop-color {
  display: inline-block;
}
.shop-color li {
  display: inline-block;
  margin-bottom: 0;
}
.shop-size {
  display: inline-block;
}
.shop-size li {
  display: inline-block;
  margin-bottom: 0;
}
.shop-size li a {
  color: #747691;
  text-transform: uppercase;
  border: 2px solid transparent;
  line-height: 1;
  padding: 10px;
  padding-bottom: 8px;
  display: inline-block;
}
.shop-size li.active a {
  border: 2px solid $primaryColor;
  color: $primaryColor;
}
.variant-item {
  padding: 30px 0;
  border-top: 1px solid #ffe7e5;
}

.details-timer {
  display: inline-block;
}
.details-timer .cdown {
  height: 60px;
  width: 60px;
  border-radius: 0;
  line-height: 60px;
  padding: 11px 0;
  background: rgba($primaryColor, 0.2);
  margin-right: 10px;
  box-shadow: none;
}
.details-timer .cdown span {
  font-size: 18px;
  color: $primaryColor;
  margin-bottom: 7px;
}
.details-timer .cdown p {
  color: #747691;
  font-weight: 400;
}
.product-coming .variant-name span {
  position: relative;
  top: -11px;
}
.product-desc p {
  margin-bottom: 0;
}
.product-info-list ul {
}
.product-info-list ul li {
  margin-bottom: 10px;
}
.product-info-list ul li span {
  font-weight: 500;
  color: #232b37;
  width: 120px;
  display: inline-block;
}
.product-info-list ul li span.in-stock {
  font-weight: 400;
  color: $primaryColor;
}
.cart-plus-minus input {
  height: 50px;
  width: 160px;
  border: 0;
  text-align: center;
  -moz-appearance: none;
  appearance: none;
  background: rgba($primaryColor, 0.2);
  border-radius: 30px;
  font-weight: 500;
  color: #747691;
  font-size: 16px;
}
.cart-plus-minus {
  display: inline-block;
  position: relative;
  margin-right: 15px;
}
.cart-plus-minus .qtybutton {
  position: absolute;
  top: 5px;
  left: 5px;
  font-size: 20px;
  color: #c4bedd;
  cursor: pointer;
  line-height: 1;
  width: 40px;
  height: 40px;
  background: $white;
  font-size: 16px;
  text-align: center;
  line-height: 40px;
  font-weight: 500;
  border-radius: 50%;
  transition: 0.3s;
}
.cart-plus-minus .qtybutton:hover {
  background: $primaryColor;
  color: $white;
}
.cart-plus-minus .inc {
  left: auto;
  right: 5px;
}
.plus-minus {
  display: inline-block;
}
.details-action-icon {
  height: 50px;
  width: 50px;
  border: 0;
  background: rgba($primaryColor, 0.2);
  border-radius: 50px;
  margin-right: 13px;
  color: $primaryColor;
  font-size: 15px;
  line-height: 50px;
}
.details-action-icon:hover {
  background: $primaryColor;
  color: $white;
}
/* review */
.pro-details-banner {
  margin-top: 84px;
}
.review-tab .nav-link {
  color: #333;
  font-size: 14px;
  font-weight: 500;
  padding: 20px 40px;
  color: #747691;
  text-transform: uppercase;
  letter-spacing: 2px;
  line-height: 1;
  border: 2px solid #ecedff;
}
.review-tab .nav-item {
  margin-right: 30px;
  position: relative;
  margin-bottom: 25px;
}
.review-tab .nav-link.active {
  background: $primaryColor;
  border-color: $primaryColor;
  color: $white;
}
.desc-text {
  padding: 40px;
  border: 4px solid #f9f9f9;
}
.pro-comments-img {
  float: left;
}
.pro-commnets-text {
  overflow: hidden;
  padding-left: 15px;
}
.pro-commnets-text h4 {
  font-size: 17px;
  margin-bottom: 0;
}
.pro-commnets-text span {
  font-size: 14px;
}
.pro-commnets-text .rating {
  margin-bottom: 20px;
}
.pro-commnets-text > p {
  margin: 0;
}
.product-commnets-list {
  border-bottom: 1px solid #e5e5e5;
}
.pro-rating i {
  font-size: 12px;
  color: $primaryColor;
}
.pro-commnets-text .pro-rating {
  margin-bottom: 5px;
  margin-top: 8px;
}
.pro-commnets-text .pro-rating {
  margin-bottom: 5px;
  margin-top: 8px;
}
.product-commnets .product-commnets-list:last-child {
  border: 0;
}

.review-box > h4 {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
}
.rating-list {
  display: inline-block;
}
.your-rating > span {
  color: #414149;
  font-size: 16px;
}
.rating-list > a {
  color: #646464;
  font-size: 13px;
}
.rating-list > a:hover {
  color: $primaryColor;
}
.review-form {
}
.review-form label {
  display: block;
  font-size: 13px;
  margin-bottom: 15px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.review-form textarea {
  border: 2px solid #ecedff;
  height: 80px;
  margin-bottom: 25px;
  padding: 15px;
  width: 100%;
}
.review-form input {
  border: 2px solid #ecedff;
  height: 50px;
  margin-bottom: 30px;
  padding: 0 15px;
  width: 100%;
}
.review-form button {
}
