/* Normal desktop :1200px. */
@media (min-width: 1200px) and (max-width: 1500px) {
	.box-90 {
		padding-left: 10px;
		padding-right: 10px;
	}

	.header-right ul li {
		margin-left: 3px;
	}

	.basic-bar {
		margin-right: 15px;
	}

	.main-menu ul li.mega-menu>ul.submenu {
		min-width: 720px;
	}

	.main-menu ul li {
		margin: 0 18px;
	}

	.slide-1-style {
		padding: 0px 65px;
	}

	.slide-content h1 {
		font-size: 72px;
	}

	.slide-content-3 h1 {
		font-size: 120px;
	}

	.slider-active button.slick-arrow {
		left: 10px;
	}

	.slider-active button.slick-next {
		left: auto;
		right: 10px;
	}

	.slider-active:hover button.slick-prev {
		left: 15px;
	}

	.slider-active:hover button.slick-next {
		right: 15px;
	}

	.top-seller img {
		width: 100%;
	}

	.upcoming-product-area {
		background-position: 35% 0px;
	}

	.upcoming-product h1 {
		font-size: 60px;
		margin-bottom: 35px;
	}

	.box-105 {
		padding-left: 30px;
		padding-right: 30px;
	}

	.subscribe-inner {
		padding-left: 50px;
		padding-right: 50px;
	}

	.product-slider.owl-carousel .owl-nav div {
		left: -25px;
	}

	.product-slider.owl-carousel .owl-nav div.owl-next {
		left: auto;
		right: -25px;
	}

	.slide-height-2 {
		min-height: 770px;
		background-position: center bottom;
	}

	.shape-icon-3 {
		right: 0;
	}

	.footer-area.pl-100.pr-100 {
		padding-left: 50px;
		padding-right: 50px;
	}

	.discover-link {
		position: static;
		transform: translateY(0);
		margin-top: 12px;
	}
}

