/* 6. Product */
.box-90 {
	padding-left: 90px;
	padding-right: 90px;
}

.box-105 {
	padding-left: 105px;
	padding-right: 105px;
}

.product-wrapper {
	overflow: hidden;
}

.product-img {
	position: relative;
	overflow: hidden;
}

.product-img > a {
}

.product-img img {
	width: 100%;
}

img.secondary-img {
	position: absolute;
	left: 0;
	top: 0;
	transition: 0.3s;
	opacity: 0;
}

.product-wrapper:hover img.secondary-img {
	opacity: 1;
	z-index: 1;
}

/* roduct-action */
.product-action {
	position: absolute;
	bottom: 40px;
	transition: 0.3s;
	z-index: 2;
	opacity: 0;
	visibility: hidden;
	left: 0;
	right: 0;
}

.product-wrapper:hover .product-action {
	opacity: 1;
	visibility: visible;
}

.product-action a {
	background: $white;
	height: 50px;
	width: 50px;
	font-size: 20px;
	display: inline-block;
	color: #8688a6;
	text-align: center;
	line-height: 51px;
	border-radius: 50%;
	margin: 0 6px;
	box-shadow: 0px 12px 24px 0px rgba(186, 186, 186, 0.3);
}

.product-wrapper:hover .product-action a {
	margin: 0 7px;
}

.product-action a:last-child {
	margin-bottom: 0;
}

.product-action a:hover {
	background: $primaryColor;
	color: $white;
}

.sale-tag {
	position: absolute;
	top: 30px;
	left: 30px;
	z-index: 99;
}

.sale-tag span {
	background: $secondaryColor;
	color: $white;
	text-transform: uppercase;
	font-weight: 500;
	padding: 7px 15px;
	font-size: 12px;
	position: relative;
	padding-left: 19px;
	z-index: 99;
	display: block;
	margin-bottom: 7px;
	letter-spacing: 2px;
}

.sale-tag span.sale {
	background: $primaryColor;
}

/* product-content */
.product-content {
	position: relative;
	padding-right: 50px;
}

.pro-cat a {
	font-size: 14px;
	color: $primaryColor;
	text-transform: uppercase;
	letter-spacing: 2px;
}

.pro-cat a:hover {
	color: $secondaryColor;
}

.product-content h4 {
	font-size: 20px;
	margin-bottom: 15px;
}

.product-content h4 a:hover {
	color: $primaryColor;
}

.pro-price span {
	font-weight: 500;
	font-size: 14px;
	color: #747691;
	margin-right: 25px;
}

.pro-price span.old-price {
	color: #bcbdce;
	text-decoration: line-through;
}

.product-wishlist {
	position: absolute;
	right: 0;
	top: 20px;
}

.product-wishlist a {
	background: #f3f4ff;
	height: 40px;
	width: 40px;
	display: inline-block;
	text-align: center;
	line-height: 41px;
	border-radius: 50%;
	color: #9798ad;
	font-size: 14px;
}

.product-wishlist a:hover {
	background: $primaryColor;
	color: $white;
}

/* product-nav */
.product-nav {
}

.product-nav li {
	margin-left: 10px;
	margin-bottom: 10px;
}

.product-nav li:last-child {
	padding-right: 0;
}

.product-nav li:first-child {
	padding-left: 0;
	margin-left: 0;
}

.product-nav li a {
	font-size: 14px;
	color: #10111e;
	font-weight: 500;
	padding: 18px 30px;
	border: 2px solid #ecedff;
	text-transform: uppercase;
	line-height: 1;
	letter-spacing: 2px;
}

.product-nav li a.active {
	color: $white;
	background: $primaryColor;
	border-color: $primaryColor;
}

.product-slider.owl-carousel .owl-nav div,
.product-slider-2.owl-carousel .owl-nav div {
	position: absolute;
	top: 50%;
	left: -70px;
	transform: translateY(-50%);
	background: none;
	border: 0;
	font-size: 13px;
	padding: 0;
	color: #10111e;
	z-index: 2;
	opacity: 1;
	visibility: visible;
	height: 100px;
	width: 30px;
	border-radius: 0;
	cursor: pointer;
	background: rgba($primaryColor, 0.2);
	line-height: 100px;
}

.product-slider.owl-carousel .owl-nav div.owl-next,
.product-slider-2.owl-carousel .owl-nav div.owl-next {
	left: auto;
	right: -70px;
	i {
		transform: scale(1);
		/*rtl:raw:
	transform: scale(-1);
	*/
	}
}

.product-slider:hover .owl-nav div,
.product-slider-2:hover .owl-nav div {
	opacity: 1;
	visibility: visible;
}

.product-slider .owl-nav div:hover,
.product-slider-2 .owl-nav div:hover {
	background: $primaryColor;
	color: $white;
	box-shadow: 0px 6px 12px 0px rgba($primaryColor, 0.4);
}

/* top seller */
.top-seller {
	position: relative;
}

.top-seller-content {
	background: $white;
	display: inline-block;
	padding: 45px;
	margin-top: -80px;
	padding-bottom: 0;
}

.top-seller-content h2 {
	font-size: 40px;
	margin-bottom: 15px;
}

.top-seller-content h2 a:hover {
	color: $primaryColor;
}

.sellet-2-content {
	display: inline-block;
	width: 450px;
	position: absolute;
	left: 0;
	background: $white;
	padding: 45px;
	text-align: left;
	top: 35px;
}

.sellet-2-content h2 {
	font-size: 80px;
}

.sellet-2-content h2 a:hover {
	color: $primaryColor;
}

.shop-btn {
	background: $primaryColor;
	height: 60px;
	width: 60px;
	font-size: 22px;
	display: inline-block;
	color: $white;
	text-align: center;
	line-height: 56px;
	border-radius: 50%;
	margin: 0 7px;
	box-shadow: 0px 12px 24px 0px rgba(186, 186, 186, 0.3);
}

.shop-btn:hover {
	background: $secondaryColor;
	color: $white;
}

.top-seller-btn a {
	display: inline-block;
	margin-right: 20px;
}
