/* 5. Features */
.width-20 {
  width: 20%;
  float: left;
  padding: 0 15px;
}
.single-seatures i {
  height: 200px;
  width: 200px;
  background: rgba($primaryColor, 0.1);
  color: $primaryColor;
  display: inline-block;
  text-align: center;
  font-size: 65px;
  border-radius: 50%;
  line-height: 204px;
}
.single-seatures h3 {
  font-size: 26px;
  margin: 0;
}
.single-seatures span {
  display: block;
  font-weight: 500;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #747691;
  margin-bottom: 20px;
  margin-top: 35px;
}
.service-bg {
  background-repeat: no-repeat;
  background-position: center center;
}
