/* 9. Subscribe   */
.subscribe-area {

  .subscribe-box {
    background-color: $yellow;

    h2,
    p {
      color: $white;
    }

    .form-box {
      position: relative;
      margin-bottom: 10px;

      input {
        background: $white;
        height: 70px;
        width: 100%;
        padding: 0 80px;
        padding-right: 30px;
        border: 0;
        color: #aab5ca;
        font-weight: 500;
        letter-spacing: 2px;
        text-transform: uppercase;
      }

      &::before {
        position: absolute;
        content: "\f0e0";
        font-family: "Font Awesome 5 Free";
        font-weight: 700;
        display: inline-block;
        font-size: 14px;
        text-align: center;
        left: 45px;
        color: #756563;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 99;
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        -o-transform: translate(-50%, -50%);
      }
    }

    .theme-btn {
      background-color: $thirdColor;
    }

  }
}