/* 12. Breadcrumb */
.breadcrumb-area {
  background-size: cover;
  padding: 120px 0;
}

.breadcrumb-text {
  position: relative;
  z-index: 1;
}

.breadcrumb-text>h1 {
  font-size: 60px;
  margin-bottom: 30px;
}

.breadcrumb-text p {
  font-size: 26px;
  color: #637595;
  margin-bottom: 50px;
}

.breadcrumb-menu {
  display: inline-block;
  border-radius: 0;
  padding: 24px 42px;
  background: $white;
  line-height: 1;
}

.breadcrumb-menu li {
  display: inline-block;
  position: relative;
  padding: 0 13px;
}

.breadcrumb-menu li a {
  color: #525470;
  font-size: 14px;
  text-transform: uppercase;
  transition: all 0.3s ease 0s;
  letter-spacing: 2px;
  font-weight: 500;
}

.breadcrumb-menu li::before {
  background: #c4c5d5;
  display: inline-block;
  left: -4px;
  padding-bottom: 0;
  position: absolute;
  top: -2px;
  height: 17px;
  width: 2px;
  content: "";
}

.breadcrumb-menu li:first-child::before {
  display: none;
}

.breadcrumb-menu span {
  color: #abadc5;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  transition: all 0.3s ease 0s;
  letter-spacing: 2px;
}

.basic-pagination ul {
  display: block;
}

.basic-pagination ul li {
  display: inline-block;
  margin: 0 5px;
}

@media (max-width: 767px) {
  .basic-pagination ul li {
    margin-bottom: 10px;
  }
}

.basic-pagination ul li a {
  height: 70px;
  width: 70px;
  background: $white;
  color: #9990b8;
  font-size: 16px;
  font-weight: 500;
  border-radius: 50%;
  line-height: 70px;
  margin: 0px;
  display: inline-block;
  text-align: center;
}

.basic-pagination ul li a:hover,
.basic-pagination ul li.active a {
  background: $primaryColor;
  color: $white;
  box-shadow: 0px 8px 16px 0px rgba($primaryColor, 0.3);
}

.basic-pagination-2 ul li {}

.basic-pagination-2 ul li a {
  background: transparent;
  color: #9990b8;
  border: 2px solid #eaebec;
  height: 60px;
  width: 60px;
  line-height: 56px;
}

.basic-pagination-2 ul li a:hover,
.basic-pagination-2 ul li.active a {
  background: $primaryColor;
  color: $white;
  border-color: $primaryColor;
  box-shadow: 0px 8px 16px 0px rgba($primaryColor, 0.3);
}