.newsletterModal {
	background: rgba($black, 0.7);

	.modal-dialog {
		max-width: 800px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%) !important;
		-webkit-transform: translate(-50%, -50%) !important;
		-moz-transform: translate(-50%, -50%) !important;
		-ms-transform: translate(-50%, -50%) !important;
		-o-transform: translate(-50%, -50%) !important;
	}

	.modal-content {
		position: relative;
		background: $primaryColor;
		border: none;
		min-height: 450px;
		padding: 60px 0;

		.close {
			position: absolute;
			left: 95%;
			top: 5%;
		}

		.modal-body {
			position: unset;
		}

		h2,
		p {
			color: $white;
		}

		.form-box {
			position: relative;
			margin-bottom: 10px;

			input {
				background: $white;
				height: 70px;
				width: 100%;
				padding: 0 80px;
				padding-right: 30px;
				border: 0;
				color: #aab5ca;
				font-weight: 500;
				letter-spacing: 2px;
				text-transform: uppercase;
			}

			&::before {
				position: absolute;
				content: "\f0e0";
				font-family: "Font Awesome 5 Free";
				font-weight: 700;
				display: inline-block;
				font-size: 14px;
				text-align: center;
				left: 45px;
				color: #756563;
				top: 50%;
				transform: translate(-50%, -50%);
				z-index: 99;
				-webkit-transform: translate(-50%, -50%);
				-moz-transform: translate(-50%, -50%);
				-ms-transform: translate(-50%, -50%);
				-o-transform: translate(-50%, -50%);
			}
		}

		.theme-btn {
			background-color: $yellow;
		}
	}

	@media (max-width: 767px) {
		.modal-content {
			background: $primaryColor;
			padding: unset;
			.row {
				padding: 15px 0;
			}
		}

		.close {
			position: absolute;
			left: 92% !important;
			top: 1% !important;
		}
	}
}
